@import url(https://fonts.googleapis.com/css?family=Signika:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Tomorrow:500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Charm:400,700&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --ff-primary: "Signika", "Roboto", sans-serif;
    --ff-secondary: "Tomorrow", "Roboto", sans-serif;

    --fw-regular: 400;
    --fw-bold: 700;

    --clr-light: #fff;
    --clr-dark: rgb(5, 13, 24);
    --clr-dark-mode: rgb(33, 46, 65);
    --clr-accent: rgb(0, 31, 170);

    --fs-h1: 3rem;
    --fs-h2: 2.25rem;
    --fs-h3: 1.15rem;
    --fs-body: 1rem;

    --box-shadow: 0.25em 0.25em 0.75em #aaa, 0.125em 0.125em 0.25em #bbb;
}

@media (max-width: 390px) {
    :root {
        --fs-h1: 2.6rem;
        --fs-h2: 1.5rem;
        --fs-h3: 0.9rem;
        --fs-body: 0.75rem;
    }
}

@media (min-width: 800px) {
    :root {
        --fs-h1: 4.5rem;
        --fs-h2: 3.75rem;
        --fs-h3: 1.5rem;
        --fs-body: 1.125rem;
    }
}

.nav {
    width: 100vw;
    height: 3.5em;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    min-height: 60px;
    background-color: inherit;
    color: inherit;
    box-shadow: var(--box-shadow);
}

.nav__logo {
    height: var(--fs-h2);
}

.nav__list {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 78vw;
    list-style: none;
    padding: 0;
}

.nav__link {
    color: inherit;
    text-decoration: none;
    font-size: var(--fs-h3);
}

.is-current {
    background-color: var(--clr-accent);
    padding: 5px;
    border-radius: 5%;
    color: var(--clr-light);
}

.intro {
    position: relative;
    background-color: inherit;
    color: inherit;

    margin-top: 3.5em;
    padding-top: 2em;
    background-image: url("/images/computer.png");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.intro__container {
    width: 70%;
    margin: 0 auto;
    padding-bottom: 2em;
}

.intro__title--first,
.intro__title--last {
    font-size: var(--fs-h1);
    padding-left: 0.5em;
    color: var(--clr-light);
}
.intro__subtitle {
    font-family: var(--ff-secondary);
    font-size: var(--fs-h3);
    text-transform: uppercase;
    color: var(--clr-accent);
    text-shadow: -0.5px 0 var(--clr-light), 0 0.5px var(--clr-light),
        0.5px 0 var(--clr-light), 0 -0.8px var(--clr-light);
}

.intro__img {
    box-shadow: var(--box-shadow);
    border-radius: 15%;
}

@media (min-width: 800px) {
    .intro {
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .intro__container {
        display: grid;
        grid-gap: 2em;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 500px;
        min-width: 80%;
        margin: 0 auto;
    }

    .intro__titles {
        grid-column: 1 / 2;
        display: grid;
        grid-template-rows: repeat(3, 100px);
        justify-self: flex-end;
    }

    .intro__img {
        min-width: 300px;
        height: 650px;
        grid-column: 2 / 3;
        margin-top: -7em;
    }

    .intro__title--first {
        grid-row: 1 / 2;
        text-align: left;
        align-self: end;
        margin: 0;
        padding: 0;
    }
    .intro__title--last {
        grid-row: 2 / 3;
        text-align: center;
        height: -webkit-max-content;
        height: -moz-max-content;
        height: max-content;
    }

    .intro__subtitle {
        grid-row: 3 / 4;
        text-align: left;
        align-self: end;
    }
}

.specialty-card__container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 auto;
}

.specialty-card__each {
    margin: 1em;
    text-align: center;
    width: 60px;
}

.lang__icon {
    font-size: var(--fs-h1);
}

@media (min-width: 800px) {
    .specialty-card__each {
        width: 70px;
    }
}

.about {
    margin: 0 1em;
    padding-top: 4em;
    background-color: inherit;
    color: inherit;
}

.about__title {
    text-align: center;
    font-size: var(--fs-h2);
}

.about__text {
    padding: 0 1em;
    font-size: var(--fs-body);
}

.about__text--phrase {
    font-size: var(--fs-h3);
    text-align: center;
    margin-top: 2em;
}

.about__specialties {
    margin: 0 1em;
    padding-top: 2em;
}

@media (min-width: 715px) {
    .about {
        min-height: 100vh;
    }

    .about__container {
        padding: 3em;
    }

    .about__specialties {
        width: 60%;
        margin: 0 auto;
    }
}

@media (min-width: 1450px) {
    .about__container {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-evenly;
        align-items: center;
    }

    .about__text,
    .about__specialties {
        max-width: 45%;
        margin: 0 3em;
    }

    .about__text p {
        font-size: var(--fs-h3);
    }

    .about__line {
        width: 2px;
        height: 60vh;
        margin-right: 10px;
        background-color: var(--clr-accent);
        -webkit-transform: rotate(13deg);
                transform: rotate(13deg);
    }
}

.project-card__container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    margin: 1em auto;
}

.project-card__each {
    margin: 3em 1.5em;
    max-width: 350px;
    height: 200px;
    box-shadow: var(--box-shadow);
}

.project-card__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    overflow: hidden;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.project-card__figcaption {
    visibility: visible;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #1d1d1dd0;
    margin: 0 auto;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    box-shadow: var(--box-shadow);
}

.project-card__name {
    color: var(--clr-light);
    font-size: var(--fs-h3);
    font-weight: var(--fw-bold);
    margin: 4px auto;
    text-align: center;
    display: block;
    width: 100%;
    text-decoration: underline;
    -webkit-text-decoration-style: double;
            text-decoration-style: double;
    -webkit-text-decoration-color: var(--clr-accent);
            text-decoration-color: var(--clr-accent);
}

.project-card__sites {
    display: block;
}

.project-card__url,
.project-card__code {
    color: var(--clr-light);
    display: inline;
    margin: 15px;
}

@media (min-width: 800px) {
    .project-card__each {
        margin: 3em 1em;
        max-width: 350px;
        min-width: 230px;
        height: 200px;
        overflow: hidden;
        position: relative;
        text-align: center;
        -webkit-transition: all 0.35s ease;
        transition: all 0.35s ease;
        border-radius: 3%;
        box-shadow: var(--box-shadow);
    }
    .project-card__each:before,
    .project-card__each:after {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        -webkit-transition: all 0.35s ease;
        transition: all 0.35s ease;
        background-color: #1d1d1d;
        content: "";
        opacity: 0.85;
        z-index: 1;
    }

    .project-card__each:before {
        -webkit-transform: skew(45deg) translateX(-180%);
                transform: skew(45deg) translateX(-180%);
    }

    .project-card__each:after {
        -webkit-transform: skew(45deg) translateX(180%);
                transform: skew(45deg) translateX(180%);
    }

    .project-card__img {
        max-width: 100%;
        vertical-align: top;
    }

    .project-card__figcaption {
        visibility: hidden;
        width: 90%;
        display: inline-block;
        background-color: transparent;
        box-shadow: none;
    }

    .project-card__each:hover .project-card__figcaption,
    .project-card__each:focus .project-card__figcaption {
        visibility: visible;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
        -webkit-transform: translate(-50%, -50%) scale(1.1);
                transform: translate(-50%, -50%) scale(1.1);
        opacity: 1;
    }

    .project-card__each:hover:before {
        -webkit-transform: skew(45deg) translateX(-49.9%);
                transform: skew(45deg) translateX(-49.9%);
    }

    .project-card__each:hover:after {
        -webkit-transform: skew(45deg) translateX(50%);
                transform: skew(45deg) translateX(50%);
    }

    .project-card__figcaption:hover {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        opacity: 1;
    }

    .project-card__name {
        color: var(--clr-light);
        font-size: var(--fs-h3);
        font-weight: var(--fw-bold);
    }

    .project-card__url,
    .project-card__code {
        display: block;
        margin: 10px auto;
        color: var(--clr-light);
    }
}

.projects {
    padding: 4em 2em 2em;
    min-height: 100vh;
    background-image: url("/images/computer.png");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.projects__title {
    font-size: var(--fs-h2);
    text-align: center;
    margin-bottom: 1em;
    color: var(--clr-light);
}

.contact {
    min-height: 50vh;
    padding: 4em 2em 2em;
}

.contact__form--title {
    text-align: center;
    font-size: var(--fs-h2);
    margin-bottom: 1em;
}

.contact__form--text {
    font-size: var(--fs-h3);
    margin: 2em 0;
}

.contact__form--form {
    font-size: var(--fs-h3);
    color: inherit;
    display: grid;
    grid-gap: 1.5em;
    grid-template-rows: repeat(3, 0.4fr) 2fr;
    grid-template-columns: 0.5fr 2fr;
}

.contact__label--name {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
}
.contact__input--name {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
}
.contact__label--email {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
}
.contact__input--email {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
}
.contact__label--msg {
    grid-row: 3 / 4;
    grid-column: 1 / 3;
    justify-self: center;
}
.contact__input--msg {
    grid-row: 4 / 5;
    grid-column: 1 / 3;
}

.contact__form--form input,
.contact__form--form textarea {
    border: 1px solid var(--clr-accent);
    border-radius: 5px;
    background-color: var(--clr-accent);
    color: var(--clr-light);
    font-size: var(--fs-body);
    font-weight: var(--fw-bold);
}

.contact__form--form label {
    display: flex;
    align-items: center;
}

.contact__form--submit {
    display: block;
    padding: 10px;
    margin: 1em auto 0;
    border: 1px solid var(--clr-accent);
    border-radius: 15%;
    background-color: var(--clr-light);
    font-size: var(--fs-body);
    font-weight: var(--fw-bold);
    color: var(--clr-accent);
    box-shadow: 2px 2px 4px #bbb;
}

@media (min-width: 600px) {
    .contact__form {
        width: 80%;
        margin: 0 auto;
        text-align: center;
    }

    .contact__form--form {
        width: 50%;
        margin: 0 auto 1.5em;
    }

    .contact__form--submit {
        margin-bottom: 1em;
    }

    .contact__form--submit:hover {
        background-color: var(--clr-accent);
        color: var(--clr-light);
        border: 1px solid var(--clr-light);
        cursor: pointer;
    }
}

.main {
    background-color: inherit;
    color: inherit;
}

.nav__list--special {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 78vw;
    list-style: none;
}

.project {
    margin: 3.5em 1em 1em;
    padding: 1em;
}

.project__name {
    font-size: var(--fs-h2);
    color: inherit;
    text-align: center;
    line-height: 1.6em;
}

.project__img {
    max-width: 90%;
    max-height: 550px;
    margin: 1.5em auto;
    border-radius: 5%;
    box-shadow: var(--box-shadow);
}

.project__description,
.project__tech,
.project__url,
.project__code {
    font-size: var(--fs-body);
}

.project__tech--title {
    text-align: center;
    font-size: var(--fs-h3);
}

.project__url,
.project__code {
    display: block;
    text-align: center;
    color: inherit;
}

.project__url {
    margin-bottom: 1em;
}

@media (min-width: 1250px) {
    .project {
        margin: 3.5em 3em 1.5em;
        padding: 3em;
        text-align: center;
    }

    .project__img {
        max-width: 70%;
        margin: 4em auto;
    }

    .project__tech--title {
        margin-top: 2em;
        margin-bottom: 2em;
    }

    .project__url {
        margin: 2em auto;
    }
}

.footer {
    bottom: 0;
    height: 10em;
    font-size: var(--fs-h3);
    font-family: "Charm", cursive;
    text-align: center;
    padding: 2.5em 0 0;
    border-radius: 300vh 300vh 0% 0% / 20vh 20vh 0% 0%;
    background-color: var(--clr-accent);
    color: var(--clr-light);
}

.footer p {
    margin-top: -1em;
    margin-bottom: 2em;
}

.footer__span {
    font-size: var(--fs-body);
    font-weight: var(--fw-regular);
}

.social-list__link:hover {
    opacity: 0.7;
}

.social-list {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 2.5em;
    margin: 0 auto;
    margin-left: -3em;
}

.social-list__item {
    margin: 0 0.5em;
}

.social-list__link {
    padding: 0.5em;
    color: var(--clr-light);
}

.App,
body {
    font-family: var(--ff-primary);
    font-size: var(--fs-body);
    line-height: 1.6em;
    background-color: var(--clr-light);
    color: var(--clr-dark);
    overflow-y: scroll;
    scroll-behavior: smooth;
    margin: 0;
    padding: 0;
    letter-spacing: 1.2px;
}

.App--night-mode {
    background-color: var(--clr-dark-mode);
    color: var(--clr-light);
}

img {
    display: block;
    max-width: 100%;
}

